import React, {Component} from 'react';
import Axios from 'axios';
import StoryList from './Components/StoryList/StoryList';
import StoryButton from './Components/StoryButton/StoryButton';
import StoryModal from './Components/StoryModal/StoryModal';
import './App.css';

export default class App extends Component {

    constructor(){
        super();

        this.state = {
            modalIsOpen: false,
            stories: [],
            story: {
                author: '',
                content: '',
                _id: undefined
            }
        };

        this.apiUrl = 'https://wt-6f60c90cb75ab3648ef60a005f2cfa2d-0.run.webtask.io/wt-mern-api/stories'

        this.handleEdit = this.handleEdit.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
        this.openModal = this.openModal.bind(this);
    }

    componentDidMount() {
        Axios.get(this.apiUrl)
        .then(({data}) => {
            this.setState({stories: data});
        })
    }

    handleEdit(id) {
        this.openModal(this.state.stories.find(x => x._id === id))
    }

    handleDelete(id) {
        Axios.delete(`${this.apiUrl}?id=${id}`)
        .then(() => {
            const updatedStories = this.state.stories.findIndex(x => x._id === id);
            this.setState({states: [...this.state.stories.splice(updatedStories, 1)]})
        })
    }

    openModal(story) {
        this.setState({modalIsOpen: true});
        if(story)
            this.setState({story})
    }

    closeModal(model){
        this.setState({modalIsOpen: false});

        if(model) {
            if(!model._id){
                Axios.post(this.apiUrl, model)
                .then(({data}) => {
                    this.setState({stories: [data, ...this.state.stories]});
                    this.setState({isLoading: false})
                })
            } else {
                Axios.put(`${this.apiUrl}?id=${model._id}`, model)
                .then(({data}) => {
                    const storyToUpdate = this.state.stories.find(x => x._id === model._id);
                    const updatedStory = Object.assign({}, storyToUpdate, data);
                    const newStories = this.state.stories.map(story => {
                        if(data._id === story._id) return updatedStory;
                        return story;
                    })
                    this.setState({stories: newStories});
                })
            }
        }

        this.setState({story: {
            author: '',
            content: '',
            _id: undefined
        }})
    }

    render() {
        return(
            <div className="App">
                <div className="col-md-4 col-md-offset-4 Story">
                    <div className="StoryHeader">
                        <h2>Stories</h2>
                    </div>

                    <StoryList
                        stories={this.state.stories}
                        handleEdit={this.handleEdit}
                        handleDelete={this.handleDelete}
                    />

                    <div className="StoryFooter">
                        <p>Thank you!</p>
                    </div>

                    <StoryModal
                        modalIsOpen={this.state.modalIsOpen}
                        story={this.state.story}
                        closeModal={this.closeModal}
                    />
                </div>
                <StoryButton handleClick={this.openModal.bind(this, null)} />
            </div>
        )
    }
}


